<template>
  <div class="fillcontain">
      
    <div class="app-head">
      <div class="app-head-inner">
        <div class="head-nav">
          <ul class="nav-list">
            <li class="nav-pile" @click="logout()">
              
            </li>
            <img src = "../assets/client.jpg" align = "left">
            <li class="nav-pile">{{this.account}}</li>
            <li class="nav-pile" @click="myModelBase()">
              
              </li>
          </ul>
        </div>  
      </div>
    </div>

    <div class="main_container">
      <div class="model_part">
        <img class = "model_pic" src = '../assets/steam.png'  style="width:80px;height:80px;">
        <span style="font-size:25px">{{modelName}}</span><span style="font-size:18px">({{trainStatus}})</span>
      </div>
    </div>
    
    <div class="top_train_block">
        <el-row type="flex" class="row-bg" justify="end">
          <el-button type="primary" @click="addLabel()""></el-button>
          <el-button type="success" @click="submitData()"></el-button>
        </el-row>
        
        <el-dialog title="" :visible.sync="addLabelVisible" :modal-append-to-body="false" ref="addType">
          <el-form :model="addDev" ref="addType">
            <el-row>
              <el-form-item label="">
                <el-input v-model="addDev.label"></el-input>
              </el-form-item>
            </el-row>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="addLabelCancel()"> </el-button>
            <el-button type="primary" @click="addLabelConfirm()"> </el-button>
          </div>
        </el-dialog>

        <!--  -->
        <div class="trainData" v-for="(item, index) in tableData" :key='index'>
          <el-form>

            <el-form-item label="" class="labelData">
              <span style="font-size:25px">{{item.label}}</span>
              <el-button size="mini" type="success" @click="deleteLabel(item)"></el-button>
            </el-form-item>
            
            <el-form-item
              label=""
              class="trainSample">
                <el-upload
                  name="img"
                  class="upload-demo"
                  action="http://127.0.0.1:8001/api/ImageClassifier/uploadImg/"
                  :show-file-list=true
                  :on-success="handleSuccess"
                  :on-preview="handlePreview"
                  :on-error="handleErr"
                  :on-remove="handleRemove"
                  :before-remove="beforeRemove"
                  :before-upload="beforeUpload"
                  :limit="10"
                  :on-exceed="handleExceed"
                  :file-list="fileList"
                  :data="uploadData"
                  list-type="picture"
                  multiple
                  align="left">
                    <div class="selectImg" align="left"><el-button type="primary" @click="selectImg(item)"></el-button></div>
                </el-upload>
              </el-form-item>
          </el-form>
        </div>
      </div>


  </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'
import { apiUrl } from '../utils/apiUrl';
import coTrainStuTextVue from './coTrainStuText.vue';
  export default {
    data() {
      return {
        account: '',              //
        token: '',                //
        sessionId: '',            //
        classId: '',              //
        role: '',                 //
        csrfToken: '',            //CSRF
        modelbasePath: '',        //
        tableData: [],            //
        addDev: {                 //
          label: '',                //
          contents:[]               //
        },
        addLabelVisible: false,   //
        uploadData:{              //
          account: '',
          modelName: '',
          delete: '',
          label: '',
          imgName: ''
        },
        modelName: '',             //
        outputData: [],            //
        trainStatus: '',     //    
        //isChange: 0,            //
      }
    },

    mounted(){
      const self = this;
      self.modelName = self.$route.params.modelName;
      var csrfTokenName = "csrftoken=";
      var tokenName = "token=";
      var userName = "username=";
      var sessionId = "sessionid=";
      var classId = "class_id=";
      var role = "role=";
      var ca = document.cookie.split(';');
      for (var i = 0; i < ca.length; i++) {
        var c = ca[i];
        while (c.charAt(0) == ' ') c = c.substring(1);
        if (c.indexOf(tokenName) != -1){
            if(c.indexOf(csrfTokenName) != -1){
                self.csrfToken = c.substring(csrfTokenName.length, c.length);
            }
            else{
                self.token = c.substring(tokenName.length, c.length);
            }
        }  
        if(c.indexOf(userName) != -1){
            self.account = decodeURIComponent(c.substring(userName.length, c.length));
        }
        if(c.indexOf(sessionId) != -1){
            self.sessionId = c.substring(sessionId.length, c.length);
        }
        if(c.indexOf(classId) != -1){
            self.classId = c.substring(classId.length, c.length);
        }
        if(c.indexOf(role) != -1){
            self.role = c.substring(role.length, c.length);
        }
      }

      if(self.role == "teacher"){
        this.modelbasePath = "/modelbaseTeacher";
      }
      else{
        self.modelbasePath = "/modelbaseStudent";
      }

      var uData = JSON.stringify({
        username:self.account,
        role:self.role,
        token:self.token,
        sessionid:self.sessionId,
        class_no:self.classId
      })
      axios.post(apiUrl.loginCheck,uData,{
        headers:{"Content-Type":"application/json;charset=utf-8"}
      }).then(function (response) {
        if(response.data.code != 1){
          alert(response.data.message);
          window.location.href = "https://homepagetest.tuopinpin.com/";
        }
      }).catch(function (error) {
        console.log(error);
      }); 

      var modelCreateData = JSON.stringify({
        userName:self.account,
        modelName:self.modelName,
      })
      axios.post(apiUrl.createImgModel,modelCreateData,{
        headers:{"Content-Type":"application/json;charset=utf-8"}
      }).then(function (response) {
        if(response == "Create Image Model Success!"){
          alert("");
        }
        else if(response == "Model Name Check Failed!"){
          alert("");
        }
      }).catch(function (error) {
        console.log(error);
      }); 
    },

    methods: {
      myModelBase(){
        /**  */
        const self = this;
        var username = self.account;
        if(username == ""){
          alert("");
          window.location.href = "https://homepagetest.tuopinpin.com/";
        }
        else{
          self.$router.push("/modelbaseTeacher");
        }
      },

      addLabel(){
        this.addDev.label = "";
        this.addDev.contents = [];
        this.addLabelVisible = true;
        this.isChange = 1;
      },

      addLabelConfirm(){
        var checkFlag = false;
        var tmp = {};
        tmp.label = this.addDev.label;
        tmp.contents = [];
        for(var item of this.tableData){
          if(item.label == tmp.label){
            alert("");
            checkFlag = true;
          }
        }
        if(this.tableData.length == 0 || checkFlag == false){
          this.tableData.push(tmp);
          this.isChange = 1;
          this.addLabelVisible = false;
        }
      },

      addLabelCancel(){
        this.addLabelVisible = false;
        this.addDev.label = '';
        this.addDev.contents = [];
      },
      deleteLabel(item){
        this.$confirm(`  ${ item.label }`, '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => { 
          // Django
          var uData = JSON.stringify({
            modelName: this.modelName,
            label: item.label
          })
          axios.post(apiUrl.deleteLabel,uData,{
            headers:{"Content-Type":"application/json;charset=utf-8"}
          }).then(function (response) {
            if(response.data == "Delete Label Success"){
              alert("");
            }
          }).catch(function (error) {
            console.log(error);
          });
          this.tableData.splice(this.tableData.indexOf(item),1);
        }).catch(() => {});
      },

      selectImg(item){
        // Django
        const self = this;
        self.uploadData.account = self.account;
        self.uploadData.modelName = self.modelName;
        self.uploadData.label = item.label;
      },

      beforeUpload(file){
        // Django
        this.uploadData.delete = '0';
        this.uploadData.imgName = file.name;
        // tableData
        for(var item of this.tableData){
          if(item.label == this.uploadData.label){
            item.contents.push(this.uploadData.imgName);
            break;
          }
        }
      },

      handleSuccess(response, file, fileList){
        if(response == "Name Check Failed!") {
          alert("");
          fileList.splice(fileList.indexOf(file),1);
        }
        else {
          alert("");
        }
        self.uploadData = {};
      },

      beforeRemove(file, fileList) {
        return this.$confirm(` ${ file.name }`);
      },

      handleRemove(file, fileList) {
        const self = this;
        var deleteLabel = '';
        for(var item of self.tableData){
          for(var content of item.contents){
            if(content == file.name){
              item.contents.splice(item.contents.indexOf(content),1);
              deleteLabel = item.label;
            }
          }
        }
        //
        let formData = new FormData();
        formData.append('modelName', self.modelName);
        formData.append('delete', '1');
        formData.append('label', deleteLabel);
        formData.append('imgName', file.name);
        axios.post(apiUrl.deleteImg,formData,{
          headers:{"Content-Type":"application/json;charset=utf-8"}
        }).then(function (response) {
          if(response.data == "logic delete Success"){
            alert("");
          }
        }).catch(function (error) {
          console.log(error);
        });
      },

      submitData(){
        /**  */
        console.log(this.tableData);
        var labels = [];
        for(var item of this.tableData){
          labels.push(item.label);
        }
        //
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          alert("");
          var uData = JSON.stringify({
            userName:this.account,
            modelName:this.modelName,
            label:labels
          })
          axios.post(apiUrl.trainImgModel,uData,{
            headers:{"Content-Type":"application/json;charset=utf-8"}
          }).then(function (response) {
            console.log(response.data)
          }).catch(function (error) {
            console.log(error);
          });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          }); 
        }
    }
  }

</script>

<style>
  .trainData{
    margin-top: 0px;
  }
  .input-file{
    margin-top: 0px
  }
  .model_part{
    margin-top: -120px;
  }
  .main_container{
    margin-top: 30px;
    margin-left: 150px;
    margin-right: 150px;
  }
  .model_pic{
    margin-top: 130px;
  }
  .notes{
    margin-left: 10px;
    color:#999999;
  }
  .top_train_block{
    margin-top: 30px;
    margin-left: 130px;
  }
  .foot_block{
    margin-top: 30px;
    margin-left: 130px;
  }
  .test_block{
    margin-top: 30px;
    margin-left: 130px;
  }
</style>



